html {
  font-family: Roboto, Arial, sans-serif;
}

body {
  margin: 0;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a, a:visited {
  text-decoration: none;
}

.header {
  box-sizing: border-box;
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 50px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  height: 40px;
}

.nav__list {
  display: flex;
}

.nav__link {
  color: #060b35;
  height: 60px;
  margin-right: 20px;
  font-size: 12px;
  line-height: 60px;
  transition-duration: .3s;
  display: flex;
}

.nav__link--last {
  margin-right: 0;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media (width <= 800px) {
  .header {
    padding: 0 20px;
  }

  .nav__link {
    margin-right: 10px;
    font-size: 10px;
  }
}

@media (width <= 575px) {
  .nav__link {
    margin-right: 8px;
    font-size: 8px;
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover.card__title {
  color: #34568b;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__name {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: -4px;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.card__star {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin: 24px 0 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.main {
  padding: 50px 40px;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}
